// sass-lint:disable no-vendor-prefixes

@import './Breakpoint';

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  border: 0;
  font: inherit;
  font-size: 100%;
  margin: 0;
  padding: 0;
  vertical-align: baseline;
}

// HTML5 display-role reset for older browsers
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
  margin: 0 auto;
  max-width: 120rem;
  min-width: 20rem;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;

  &::before,
  &::after {
    content: '';
  }
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

:root {
  font-size: 12px;

  @include breakpoint('s', 'm') {
    font-size: 14px;
  }

  @include breakpoint('m', 'xl') {
    font-size: 16px;
  }

  @include breakpoint('xl') {
    font-size: 20px;
  }
}

* {
  -ms-hyphens: none;
  -webkit-hyphens: none;
  box-sizing: border-box;
  hyphens: none;
}
